import { NavLink, Outlet, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useEffect, useReducer } from 'react';
import Message from '../../components/Message';
import {
  initState,
  MessageContext,
  messageReducer,
} from '../../store/messageStore';

export default function Dashboard() {
  const reducer = useReducer(messageReducer, initState);
  const navigate = useNavigate();
  const logout = () => {
    document.cookie = 'emmaToken=;';
    navigate('/login');
  };
  const goToFront = () => {
    navigate('/');
  };
  //每次初始化就從cookie取出token，這樣已經登入過一次就不必再次登入
  const token = document.cookie
    .split('; ')
    .find((row) => row.startsWith('emmaToken='))
    ?.split('=')[1];

  // 設定 token 到 axios 全域 headers
  axios.defaults.headers.common['Authorization'] = token;

  useEffect(() => {
    if (!token) {
      return navigate('/login');
    }
    (async () => {
      try {
        await axios.post('/v2/api/user/check');
      } catch (err) {
        if (!err.response.data.success) {
          navigate('/login');
        }
      }
    })();
  }, [navigate, token]);

  return (
    <>
      <MessageContext.Provider value={reducer}>
        <Message />
        <nav className="navbar navbar-expand-lg bg-dark">
          <div className="container-fluid">
            <p className="text-white mb-0">HEX EATS 後台管理系統</p>
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarNav"
              aria-controls="navbarNav"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon" />
            </button>
            <div
              className="collapse navbar-collapse justify-content-end"
              id="navbarNav"
            >
              <ul className="navbar-nav">
                <li className="nav-item">
                  <button
                    type="button"
                    className="btn btn-sm btn-light me-2"
                    onClick={logout}
                  >
                    登出
                  </button>
                </li>
                <li className="nav-item">
                  <button
                    type="button"
                    className="btn btn-sm btn-light"
                    onClick={goToFront}
                  >
                    回到前台
                  </button>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        <div className="d-flex" style={{ minHeight: 'calc(100vh - 56px)' }}>
          <div className="bg-light" style={{ width: '200px' }}>
            <ul className="list-group list-group-flush">
              <NavLink
                className="list-group-item list-group-item-action py-3"
                to="/admin/products"
              >
                <i className="bi bi-cup-fill me-2" />
                產品列表
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action py-3"
                to="/admin/coupons"
              >
                <i className="bi bi-ticket-perforated-fill me-2" />
                優惠卷列表
              </NavLink>
              <NavLink
                className="list-group-item list-group-item-action py-3"
                to="/admin/orders"
              >
                <i className="bi bi-receipt me-2" />
                訂單列表
              </NavLink>
            </ul>
          </div>
          <div className="w-100">{token && <Outlet />}</div>
        </div>
      </MessageContext.Provider>
    </>
  );
}
